html {
    position: relative;
    min-height: 100%;
    margin-top: 50px;
    margin-bottom: 50px;
}

body {
  margin: 0;
  margin-bottom: 60px;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.formLabel {
  font-weight: bold;
  display: inline-block;
  width: 200px;
}

small {
  color: grey;
  font-size: 70%;
}

.wrapper {
  margin-top: 20px;
}

.media-wrapper {
  margin-bottom: 20px;
}

.entitystyle {
  color: grey;
  font-size: 80%;
  display:inline-block;
  width: 50px;
}

.jobTotalDuration {
  color:grey;
  font-size:80%;
}

.footerItem {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  height:75px;
}

.coursetitle {
  font-size: 120%;
}

.projectsubtitle {
  font-style: italic;
}

